"use client";

import { useState, useEffect, useCallback } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from "@/components/ui/alert-dialog";
import { Skeleton } from "@/components/ui/skeleton";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import Papa from "papaparse";
import ExcelJS from "exceljs";
import { saveAs } from "file-saver";
import Link from "next/link";

export default function SoutenancesPage() {
  const [soutenances, setSoutenances] = useState([]);
  const [filteredSoutenances, setFilteredSoutenances] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [groups, setGroups] = useState([]);
  const [salles] = useState(["Room 101", "Room 102", "Room 103"]); // Static rooms
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [statusFilter, setStatusFilter] = useState("all");
  const [activeTab, setActiveTab] = useState("all");
  const [yearFilter, setYearFilter] = useState("all");
  const [locationFilter, setLocationFilter] = useState("all");
  const [juryFilter, setJuryFilter] = useState("all");
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;

  const currentYear = new Date().getFullYear().toString();
  const juryMembers = ["Prof. Smith", "Dr. Jones", "Dr. Brown"]; // Static jury members

  // Fetch soutenances from API
  const fetchSoutenances = useCallback(async () => {
    try {
      setIsLoading(true);
      const response = await fetch("http://localhost:5000/api/soutenance");
      if (!response.ok) throw new Error("Failed to fetch soutenances");
      const data = await response.json();
      const normalizedData = data.map((s) => ({
        ...s,
        groupNames: Array.isArray(s.groupNames) ? s.groupNames : [s.groupNames],
        juryNames: Array.isArray(s.juryNames) ? s.juryNames : [s.juryNames],
      }));
      setSoutenances(normalizedData);
    } catch (err) {
      setError(err.message || "An error occurred while fetching soutenances");
    } finally {
      setIsLoading(false);
    }
  }, []);

  // Fetch groups and set static salles
  const fetchGroupsAndSalles = useCallback(async () => {
    try {
      const response = await fetch("http://localhost:5000/api/groups");
      if (!response.ok) throw new Error("Failed to fetch groups");
      const data = await response.json();
      setGroups(data);
    } catch (err) {
      setError(err.message || "An error occurred while fetching groups");
    }
  }, []);

  // Initial data fetch
  useEffect(() => {
    fetchSoutenances();
    fetchGroupsAndSalles();
  }, [fetchSoutenances, fetchGroupsAndSalles]);

  // Apply filters
  const applyFilters = () => {
    let filtered = [...soutenances];
    if (searchTerm) {
      filtered = filtered.filter(
        (s) =>
          s.groupNames.some((groupId) => {
            const group = groups.find((g) => g.idGroupe === groupId);
            return (
              group &&
              (group.nomGroupe.toLowerCase().includes(searchTerm.toLowerCase()) ||
                group.students.some((student) =>
                  student.toLowerCase().includes(searchTerm.toLowerCase())
                ))
            );
          }) ||
          s.juryNames.some((jury) =>
            jury.toLowerCase().includes(searchTerm.toLowerCase())
          ) ||
          s.location.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }
    if (statusFilter !== "all") {
      filtered = filtered.filter((s) => s.status === statusFilter);
    }
    if (yearFilter !== "all") {
      filtered = filtered.filter(
        (s) => new Date(s.date).getFullYear().toString() === yearFilter
      );
    }
    if (locationFilter !== "all") {
      filtered = filtered.filter((s) => s.location === locationFilter);
    }
    if (juryFilter !== "all") {
      filtered = filtered.filter((s) =>
        s.juryNames.some((jury) => jury === juryFilter)
      );
    }
    setFilteredSoutenances(filtered);
    setCurrentPage(1); // Reset to first page on filter change
  };

  useEffect(() => {
    applyFilters();
  }, [
    searchTerm,
    statusFilter,
    yearFilter,
    locationFilter,
    juryFilter,
    soutenances,
    groups,
  ]);

  // Pagination
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentSoutenances = filteredSoutenances.slice(indexOfFirstItem, indexOfLastItem);
  const totalPages = Math.ceil(filteredSoutenances.length / itemsPerPage);

  // Tab counts
  const getTabCount = (status) => {
    if (status === "all") return soutenances.length;
    return soutenances.filter((s) => s.status === status).length;
  };

  // Delete handler with year check
  const handleDelete = async (id) => {
    const soutenance = soutenances.find((s) => s.idSoutenance === id);
    if (!soutenance) {
      setError("Soutenance not found");
      return;
    }
    const soutenanceYear = new Date(soutenance.date).getFullYear().toString();
    if (soutenanceYear !== currentYear) {
      setError("Cannot delete soutenances from previous years");
      return;
    }
    try {
      const response = await fetch(`http://localhost:5000/api/soutenance/${id}`, {
        method: "DELETE",
      });
      if (!response.ok) {
        const errorData = await response.text();
        throw new Error(`Server error: ${errorData}`);
      }
      setSoutenances((prev) => prev.filter((s) => s.idSoutenance !== id));
      console.log("✅ Soutenance deleted successfully!");
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : "Unknown error";
      console.error("❌ Deletion error:", errorMessage);
      setError(`Failed to delete soutenance: ${errorMessage}`);
    }
  };

  // CSV Export using PapaParse
  const handleDownloadCSV = () => {
    const csvData = filteredSoutenances.flatMap((soutenance) =>
      soutenance.groupNames.map((groupId) => {
        const group = groups.find((g) => g.idGroupe === groupId);
        return {
          Jury: soutenance.juryNames.join(", "),
          Days: `${new Date(soutenance.date).toLocaleDateString("fr-FR")} à ${soutenance.time}`,
          "N°": group ? group.nomGroupe : groupId,
          Nom: group ? group.students.join(", ") : "N/A",
          Lieu: soutenance.location,
          Status: soutenance.status,
        };
      })
    );
    const csv = Papa.unparse(csvData);
    const blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });
    saveAs(blob, "soutenances.csv");
  };

  // Excel Export
  const handleExportExcel = async () => {
    const workbook = new ExcelJS.Workbook();
    const worksheet = workbook.addWorksheet("Soutenances");
    worksheet.columns = [
      { header: "Jury", key: "jury", width: 30 },
      { header: "Date", key: "date", width: 20 },
      { header: "Time", key: "time", width: 10 },
      { header: "Group", key: "group", width: 15 },
      { header: "Students", key: "students", width: 40 },
      { header: "Location", key: "location", width: 20 },
      { header: "Status", key: "status", width: 15 },
    ];
    filteredSoutenances.forEach((soutenance) => {
      soutenance.groupNames.forEach((groupId) => {
        const group = groups.find((g) => g.idGroupe === groupId);
        worksheet.addRow({
          jury: soutenance.juryNames.join(", "),
          date: new Date(soutenance.date).toLocaleDateString("fr-FR"),
          time: soutenance.time,
          group: group ? group.nomGroupe : groupId,
          students: group ? group.students.join(", ") : "N/A",
          location: soutenance.location,
          status: soutenance.status,
        });
      });
    });
    worksheet.getRow(1).font = { bold: true };
    const buffer = await workbook.xlsx.writeBuffer();
    const blob = new Blob([buffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" });
    saveAs(blob, "soutenances.xlsx");
  };

  // Status Badge Component
  const StatusBadge = ({ status }) => {
    const variant =
      status === "Pending" ? "secondary" :
      status === "Scheduled" ? "default" :
      status === "Completed" ? "success" :
      status === "Cancelled" ? "destructive" : "outline";
    return <Badge variant={variant}>{status}</Badge>;
  };

  if (error) return <div className="p-4 text-red-500">{error}</div>;

  return (
    <div className="container mx-auto p-4">
      <Card>
        <CardHeader>
          <CardTitle>Gestion des Soutenances</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col space-y-4">
            {/* Search and Filters */}
            <div className="flex space-x-4">
              <Input
                placeholder="Rechercher par groupe, jury ou lieu..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="max-w-sm"
              />
              <Select value={yearFilter} onValueChange={setYearFilter}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Année" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Toutes</SelectItem>
                  <SelectItem value="2023">2023</SelectItem>
                  <SelectItem value="2024">2024</SelectItem>
                  <SelectItem value="2025">2025</SelectItem>
                </SelectContent>
              </Select>
              <Select value={locationFilter} onValueChange={setLocationFilter}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Lieu" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Tous</SelectItem>
                  {salles.map((salle) => (
                    <SelectItem key={salle} value={salle}>{salle}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
              <Select value={juryFilter} onValueChange={setJuryFilter}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Jury" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Tous</SelectItem>
                  {juryMembers.map((jury) => (
                    <SelectItem key={jury} value={jury}>{jury}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* Tabs */}
            <Tabs value={activeTab} onValueChange={(val) => { setActiveTab(val); setStatusFilter(val); }}>
              <TabsList>
                <TabsTrigger value="all">Tous ({getTabCount("all")})</TabsTrigger>
                <TabsTrigger value="Pending">En attente ({getTabCount("Pending")})</TabsTrigger>
                <TabsTrigger value="Scheduled">Planifié ({getTabCount("Scheduled")})</TabsTrigger>
                <TabsTrigger value="Completed">Terminé ({getTabCount("Completed")})</TabsTrigger>
                <TabsTrigger value="Cancelled">Annulé ({getTabCount("Cancelled")})</TabsTrigger>
              </TabsList>
            </Tabs>

            {/* Export Buttons */}
            <div className="flex space-x-2">
              <Button onClick={handleDownloadCSV}>Exporter en CSV</Button>
              <Button onClick={handleExportExcel}>Exporter en Excel</Button>
              <Link href="/soutenances/add">
                <Button>Ajouter une soutenance</Button>
              </Link>
            </div>

            {/* Table */}
            {isLoading ? (
              <div className="space-y-2">
                {[...Array(5)].map((_, i) => (
                  <Skeleton key={i} className="h-12 w-full" />
                ))}
              </div>
            ) : (
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Date</TableHead>
                    <TableHead>Heure</TableHead>
                    <TableHead>Lieu</TableHead>
                    <TableHead>Groupes</TableHead>
                    <TableHead>Jury</TableHead>
                    <TableHead>Statut</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {currentSoutenances.length > 0 ? (
                    currentSoutenances.map((soutenance) => {
                      const isCurrentYear = new Date(soutenance.date).getFullYear().toString() === currentYear;
                      return (
                        <TableRow key={soutenance.idSoutenance}>
                          <TableCell>{new Date(soutenance.date).toLocaleDateString("fr-FR")}</TableCell>
                          <TableCell>{soutenance.time}</TableCell>
                          <TableCell>{soutenance.location}</TableCell>
                          <TableCell>
                            {soutenance.groupNames.map((groupId) => {
                              const group = groups.find((g) => g.idGroupe === groupId);
                              return group ? (
                                <div key={groupId}>
                                  {group.nomGroupe} ({group.students.join(", ")})
                                </div>
                              ) : (
                                groupId
                              );
                            })}
                          </TableCell>
                          <TableCell>
                            <TooltipProvider>
                              <Tooltip>
                                <TooltipTrigger>{soutenance.juryNames.join(", ")}</TooltipTrigger>
                                <TooltipContent>
                                  {soutenance.juryNames.join(", ")}
                                </TooltipContent>
                              </Tooltip>
                            </TooltipProvider>
                          </TableCell>
                          <TableCell>
                            <StatusBadge status={soutenance.status} />
                          </TableCell>
                          <TableCell>
                            {isCurrentYear && (
                              <div className="flex space-x-2">
                                <Link href={`/soutenances/edit/${soutenance.idSoutenance}`}>
                                  <Button variant="outline" size="sm">Modifier</Button>
                                </Link>
                                <AlertDialog>
                                  <AlertDialogTrigger asChild>
                                    <Button variant="destructive" size="sm">Supprimer</Button>
                                  </AlertDialogTrigger>
                                  <AlertDialogContent>
                                    <AlertDialogHeader>
                                      <AlertDialogTitle>Confirmer la suppression</AlertDialogTitle>
                                      <AlertDialogDescription>
                                        Êtes-vous sûr de vouloir supprimer cette soutenance ?
                                      </AlertDialogDescription>
                                    </AlertDialogHeader>
                                    <AlertDialogFooter>
                                      <AlertDialogCancel>Annuler</AlertDialogCancel>
                                      <AlertDialogAction onClick={() => handleDelete(soutenance.idSoutenance)}>
                                        Supprimer
                                      </AlertDialogAction>
                                    </AlertDialogFooter>
                                  </AlertDialogContent>
                                </AlertDialog>
                              </div>
                            )}
                          </TableCell>
                        </TableRow>
                      );
                    })
                  ) : (
                    <TableRow>
                      <TableCell colSpan={7} className="text-center">
                        Aucune soutenance trouvée
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            )}

            {/* Pagination */}
            {!isLoading && filteredSoutenances.length > itemsPerPage && (
              <div className="flex justify-between items-center p-4">
                <Button
                  onClick={() => setCurrentPage((p) => Math.max(p - 1, 1))}
                  disabled={currentPage === 1}
                >
                  Précédent
                </Button>
                <span>Page {currentPage} sur {totalPages}</span>
                <Button
                  onClick={() => setCurrentPage((p) => Math.min(p + 1, totalPages))}
                  disabled={currentPage === totalPages}
                >
                  Suivant
                </Button>
              </div>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}